import React from 'react';
import CodeCollapse from '../antd-collapse/example/code-collapse'
import { Button } from 'antd';

const divStyle ={ padding:'0 10px'}

export default class ADButton extends React.Component {
    btnList1={
      content:()=>{
        return(         
            <div style={divStyle}>
               <span>1</span>
               <Button>Default</Button>
               <Button type="primary">Primary</Button>
               <Button type="dashed">Dashed</Button>
               <Button type="danger">Danger</Button>
               <Button type="link">Link</Button>  
            </div>           
        )
      },
      code:`
        import { Button } from 'antd';

        <Button type="link">Link</Button> //[5]
      `
    }
    btnList2={
      content:()=>{
        return(         
            <div style={divStyle}>
               <span>2</span>
               <Button>Default</Button>
               <Button type="primary">Primary</Button>
               <Button type="dashed">Dashed</Button>
               <Button type="danger">Danger</Button>
               <Button type="link">Link</Button>  
            </div>           
        )
      },
      code:`
        import { Button } from 'antd';

        <Button type="link">Link</Button> //[5]
      `
    }
    render() {
        return (
           <div className="ad-button">
               <h2>Antd-</h2>
               <CodeCollapse collapseList={['1']} code={this.btnList1.code} content={this.btnList1.content}>
                 <Button type="link">Link</Button>
               </CodeCollapse>
               <CodeCollapse collapseList={['1']} code={this.btnList2.code} content={this.btnList2.content}>
                 <Button type="link">Link</Button>
               </CodeCollapse>
           </div>
        )
    }
}