import React from "react";
import RenderMenu from './render-menu'
import { Menu } from "antd";
import { withRouter } from "react-router";

 class ADLeftMenu extends React.Component {
  state = {
    current: "",
  };
  // 
  handleClick = e => {
    console.log("click ", e);
    console.log(this.props)
    this.setState({
      current: e.key
    });
    // let { history } = this.props
    // let targetProps = e.item.props   
    // this.setState({
    //   current: e.key
    // });
    // if(targetProps.path){
    //   history.push(`/ant-design${targetProps.path}`)     
    // } else {
    //   history.push(`/ant-design/no-page`)      
    // }
  };
  componentDidMount(){
    let { location } = this.props
    this.setState({current:location.pathname.replace('/ant-design/','')}) //
  }
  render() {
    let { menu } = this.props
    // console.log('this.props',this.props)
    return (
      /* 
         selectedKeys ['mail']
         mode,horizontal vertical inline
      */
      menu.length>0&&<div className="left-menu">
      <Menu
        onClick={this.handleClick}
        selectedKeys={[this.state.current]}
        theme="dark"
        mode="inline"
        className="inline-menu"
      >
        { RenderMenu(menu) }
      </Menu>
    </div>
    );
  }
}
export default withRouter(ADLeftMenu)
